.fuel-cost {
  h2 {
    margin-bottom: 48px;
  }
  table {
    width: 100%;
    border-collapse: collapse;

    thead {
      border: 1px solid #043c77;
      border-bottom: none;
    }

    th {
      text-align: left;
      padding: 19px 0 21px 30px;
      background: #043c77;

      font-weight: 600;
      font-size: 13px;
      line-height: 16px;
      color: #ffffff;

      &:first-child {
        padding-left: 20px;
      }
    }
    td {
      text-align: left;
      padding: 16px 0 16px 30px;

      font-weight: 600;
      font-size: 13px;
      line-height: 16px;

      border-bottom: 1px solid #e7e7e7;
      border-top: 1px solid #e7e7e7;

      &:last-child {
        border-right: 1px solid #e7e7e7;
      }
    }
  }
}

.td-title-style {
  padding-left: 20px;
  background: #098fcf;
  font-weight: 700 !important;
  font-size: 24px !important;
  line-height: 29px !important;
  color: #ffffff !important;
  border-left: 1px solid #098fcf !important;
  width: 163px;
}

.bl {
  border-left: 1px solid #e7e7e7;
}

.table-middle {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media (max-width: 320px) {
  .fuel-cost h2 {
    margin-bottom: 20px;
  }

  table {
    display: block;
    overflow-x: auto;
  }

  .fuel-cost table td {
    text-align: center;
    padding: 10px 4px 10px 4px;
    font-size: 10px;
    line-height: 12px;
  }

  .fuel-cost table th:first-child {
    padding-left: 0px;
  }

  .fuel-cost table th {
    padding: 10px 0 10px 6px;
    font-size: 8px;
    line-height: 10px;
  }

  .td-title-style {
    padding-left: 0px;
    font-size: 16px !important;
    line-height: 18px !important;
    width: 60px;
  }

  .table-middle {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}
